.header-image {
  max-height: 60vh;
  overflow: hidden;

  img {
    width: 100vw;
    display: block;
  }
}

.site-header {
  position: relative;
  padding: 1em;
  font-family: $font__sans;
  color: #fff;
  background-color: $color__skin;

  @media screen and (min-width: $query__small) {
    padding: 1em 2em;
  }
}

.site-branding {
  min-height: 65px;
  display: flex;

  .custom-logo-link {
    margin-right: 1em;

    img {
      display: block;
      height: 65px;
      width: auto;
    }
  }

  .site-branding-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 65px;
  }
}

.site-title {
  margin: 0;
  padding: 0;
  font-size: 1.6em;
  font-weight: 700;
  line-height: 1em;

  a {
    color: white;
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
}

.site-description {
  margin: 0;
  font-size: .9em;
  font-style: italic;
  font-weight: 100;
}

@media screen and (min-width: $query__medium) {
  .site-header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .site-branding {
    width: 35%;
    flex: 1 0 auto;
  }

  .main-navigation {
    width: auto;
  }
}

/*
Header Menu
